/* eslint-disable no-plusplus */
import React, { useRef } from "react";
import Temp from "assets/images/temp.jpg";
import Left from "assets/icons/white chevron left.svg";
import Right from "assets/icons/white chevron right.svg";
import Image from "./Image";

const ImageSlider = () => {
  const slider = useRef(null);

  const scrollAmount = 128;

  const getImages = () => {
    const imgs = [];
    for (let i = 0; i < 20; ++i) {
      imgs.push(
        <Image src={Temp} key={i} name="Name" occupation="Occupation" />
      );
    }
    return imgs;
  };

  const scrollLeft = () => {
    slider.current.scroll({
      left: slider.current.scrollLeft - scrollAmount,
      behavior: "smooth",
    });
  };

  const scrollRight = () => {
    slider.current.scroll({
      left: slider.current.scrollLeft + scrollAmount,
      behavior: "smooth",
    });
  };

  return (
    <div className="flex overflow-auto hidescroll mx-auto" ref={slider}>
      <div className="flex mx-auto">
        {getImages()}
        <div className="flex items-center">
          <input
            type="image"
            className="cursor-pointer absolute rounded-full w-8 bg-secondary left-0 ml-2 outline-none"
            onClick={scrollLeft}
            src={Left}
            alt="left"
          />
        </div>
        <div className="flex items-center">
          <input
            type="image"
            className="cursor-pointer absolute rounded-full w-8 bg-secondary right-0 mr-2 outline-none"
            onClick={scrollRight}
            src={Right}
            alt="right"
          />
        </div>
      </div>
    </div>
  );
};

export default ImageSlider;
