import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";

const NavLink = ({ to, toggle, icon, text }) => {
  return (
    <div className="flex items-center flex-row text-left border-solid border-gray-border border-t-0.5 lg:border-t-0 lg:w-auto py-3 px-8 sm:px-0 lg:py-0">
      <Link to={to} onClick={toggle} className="flex items-center flex-row">
        <div className="mr-4 lg:hidden">{icon}</div>
        {text}
      </Link>
    </div>
  );
};

NavLink.propTypes = {
  to: PropTypes.string.isRequired,
  toggle: PropTypes.func.isRequired,
  icon: PropTypes.element.isRequired,
  text: PropTypes.string.isRequired,
};

export default NavLink;
