// Deal with this later.
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React, { useState } from "react";
import PropTypes from "prop-types";
import CloseIcon from "../../../assets/icons/close.svg";
import { Header } from "../../../components/Typography";

const Card = ({ name, occupation, src, children }) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleIsOpen = () => setIsOpen(!isOpen);

  return (
    <>
      <div
        className="text-center shadow-xl rounded-lg p-8 my-16 cursor-pointer bg-white"
        onClick={toggleIsOpen}
      >
        <img
          src={src}
          alt={`${name}`}
          className="rounded-full h-24 w-24 relative mx-auto -my-6"
          style={{ top: -54 }}
        />
        <Header tag="h3">{name}</Header>
        {occupation}
      </div>
      <ExtraCardInfo
        name={name}
        occupation={occupation}
        src={src}
        isOpen={isOpen}
        toggleIsOpen={toggleIsOpen}
      >
        {children}
      </ExtraCardInfo>
    </>
  );
};

Card.propTypes = {
  name: PropTypes.string.isRequired,
  occupation: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
};

const ExtraCardInfo = ({
  name,
  occupation,
  src,
  isOpen,
  toggleIsOpen,
  children,
}) => (
  <>
    <div
      className="top-0 left-0 fixed transition-opacity duration-500 ease-in-out"
      style={{
        backgroundColor: "#000",
        opacity: isOpen ? 0.8 : 0,
        zIndex: 9999,
        width: isOpen ? "100vw" : 0,
        height: isOpen ? "100vh" : 0,
      }}
      onClick={toggleIsOpen}
    />
    <div
      className="w-screen fixed bottom-0 left-0 z-10 text-center"
      style={{
        zIndex: 10000,
        transform: isOpen ? "translateY(0%)" : "translateY(100%)",
      }}
    >
      <div
        className="bg-white transition-all duration-500 ease-in-out"
        style={{
          transform: isOpen ? "translateY(0%)" : "translateY(125%)",
          borderRadius: "2rem 2rem 0 0",
        }}
      >
        <img
          src={src}
          alt={`${name}`}
          className="rounded-full h-32 w-32 md:h-48 md:w-48 md-top-96 relative mx-auto -my-24 md:-my-16"
          style={{ top: -64 }}
        />
        <img
          src={CloseIcon}
          alt="X"
          className="absolute right-0 top-0 mr-8 mt-8 cursor-pointer"
          onClick={toggleIsOpen}
        />
        <div className="sm:max-w-sm w-4/5 mx-auto p-4">
          <h2 className="text-6xl font-bold">{name}</h2>
          <Header tag="h2">{occupation}</Header>
          {children}
        </div>
      </div>
    </div>
  </>
);

ExtraCardInfo.propTypes = {
  name: PropTypes.string.isRequired,
  occupation: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  isOpen: PropTypes.bool.isRequired,
  toggleIsOpen: PropTypes.func.isRequired,
  children: PropTypes.node.isRequired,
};

export default Card;
