import React from "react";
import PropTypes from "prop-types";
import { Header, Body } from "components/Typography";

const Image = ({ src, name, occupation }) => {
  return (
    <div className="w-32 h-32 relative Image-container">
      <img
        src={src}
        alt={name}
        className="w-full h-auto block transition-all duration-500 ease-in-out Image"
      />
      <div className="absolute text-center inset-0 transition-all duration-500 ease-in-out flex flex-no-wrap items-center justify-center Image-text">
        <div>
          <Header tag="h4">{name}</Header>
          <Body>{occupation}</Body>
        </div>
      </div>
    </div>
  );
};

Image.propTypes = {
  src: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  occupation: PropTypes.node.isRequired,
};

export default Image;
