import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Logo from "../../assets/images/logo.png";

const NavBrand = ({ children, toggle }) => {
  return (
    <div className="flex-grow space-x-4 flex py-3 px-8 sm:px-0">
      <Link
        to="/"
        onClick={toggle}
        className="flex items-center flex-shrink-0 mr-6"
      >
        <img className="mr-2 h-10 w-10" src={Logo} alt="logo" />
        <span className="font-semibold text-xl tracking-tight hidden sm:inline">
          {children}
        </span>
      </Link>
    </div>
  );
};

NavBrand.propTypes = {
  children: PropTypes.string.isRequired,
  toggle: PropTypes.func.isRequired,
};

export default NavBrand;
